body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Dosis", "Arial",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
}

.App-logo {
}

.App-header {
}

.App-link {
  color: #09d3ac;
}

.MainPhotoSection {
  color: white;
}

.Name {
  text-align: 'center';
  vertical-align: 'middle';
  font-family: 'Arial';
  font-size: 30;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.4em;
}

.Btn {
  display: inline-block;
  border: 2px solid transparent;
  border-radius: 2px;
  color: #111;
  background: rgba(255,255,255, .8);
  height: auto;
  padding: 10px 10px;
  width: 100px;
  font-size: 12px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  box-shadow: none;
  vertical-align: middle;
  white-space: nowrap;
  align-self: center;
  margin: 10px;
  -webkit-transition: background-color 0.5s ease;
  transition: background-color 0.5s ease;
}

.Btn:hover {
  background: #fff;
  opacity: 1;
  text-decoration: none;
  color: #111;
}

.link {
  color: black;
}

.link:hover {
    color: black;
    text-decoration: none;
}

.page-section {
    background-color: #fff;
    margin: auto;
    display: block;
    position: relative;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    box-sizing: border-box;
    padding: 50px 15px;
    margin-left: auto;
    margin-right: auto;
}


