.contact-form form{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap:20px;
}

.contact form label{
  display:block;
}

.contact-form input {
  height: 37px;
  padding-left: 13px;
  padding-right: 13px;
  font-size: 12px;
  letter-spacing: 1px;
  color: #777;
  border: 1px solid rgba(0,0,0, .1);
}

.contact-form input button {
  height: 37px;
  padding-left: 13px;
  padding-right: 13px;
  font-size: 12px;
  letter-spacing: 1px;
  color: #777;
  border: 1px solid rgba(0,0,0, .1);
}

input[type=submit] {
    height: auto;
    padding: 8px 37px;
    font-size: 12px;
    box-sizing: border-box;
    color: #fff;
    background: rgba(34,34,34, .9);
    border: 2px solid transparent;
    font-weight: 400;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 2px;
 }

 input[type=submit]:hover{
    font-weight: 400;
    color: rgba(255,255,255, .85);
    background: rgba(0,0,0, .7);
    text-decoration: none;
    outline: none;
    border-color: transparent;
  }


textarea:focus, input:focus{
    outline-color: black;
    outline-width: 0.1px;
}


.contact-form textarea {
  height: 130px;
  padding: 13px;
  font-size: 12px;
  letter-spacing: 1px;
  color: #777;
  border: 1px solid rgba(0,0,0, .1);
}


.contact-form form label{
  display:block;
}

.contact-form form p{
  margin:0;
}

.contact-form form .full{
  grid-column: 1 / 3;
}

.contact-form form button, .contact-form form input, .contact-form form textarea{
  width:100%;
}



.contact{
  background:#f9feff;
}

/* FORM STYLES */
.contact form{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap:20px;
}

.contact form label{
  display:block;
}

.contact form p{
  margin:0;
}

.contact form .full{
  grid-column: 1 / 3;
}

.contact form button, .contact form input, .contact form textarea{
  width:100%;
  padding:1em;
  border:1px solid #c9e6ff;
}

.contact form button{
  background:#c9e6ff;
  border:0;
  text-transform: uppercase;
}

.contact form button:hover,.contact form button:focus{
  background:#92bde7;
  color:#fff;
  outline:0;
  -webkit-transition: background-color 2s ease-out;
  transition: background-color 2s ease-out;
}

$card-width:  450px;
$card-height: 500px;
$h-color:     #9CC9E3;
$yellow:      #D0BB57;
$txt-color:   #DCE3E7;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.card-info{
  top: 200px;
  height: 280px;
  width: 100%;
  position: absolute;
  margin: 0 auto;
  padding: 0 50px;
  color: black;
  font-family: 'Arial', sans-serif;
  font-style: 16px;
  line-height: 24px;
  z-index: 20;
  opacity: 0;
  -webkit-transition: all 0.3s cubic-bezier(.33,.66,.66,1);
  transition: all 0.3s cubic-bezier(.33,.66,.66,1);
}

.blog-card{
  align-self: 'center';
  background-position: center;
  background-size: contain;

  width: 390px;
  height: 240px;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 3px 3px 20px rgba(0, 0, 0, .5);
  text-align: center;
  margin: 30px;
}

@media (max-width: 672px){
.blog-card {
  width: 310px;
  height: 200px;

}
}

.blog-card:hover{

}

.blog-card:hover .card-info{
  opacity: 1;
  top: 120px;
}

.blog-card:hover .color-overlay{
  opacity: 0.9
}

.blog-card:hover .gradient-overlay{
  opacity: 1
}

.color-overlay{
    /* Rectangle 11: */
    background: rgba(250,250,250, .9);
    width: 450px;
    height: 280px;
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    -webkit-transition: background 0.3s ease;
    transition: background 0.3s ease;
    -webkit-transition: opacity 0.3s cubic-bezier(.33,.66,.66,1);
    transition: opacity 0.3s cubic-bezier(.33,.66,.66,1);
    opacity: 0;
  }
  .gradient-overlay{
    /* bg-gradient: */
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.00)), color-stop(21%, rgba(0,0,0,.6)));
    background-image: linear-gradient(rgba(0,0,0,0.00) 0%, rgba(0,0,0,.6) 21%);
    width: 450px;
    height: 280px;
    position: absolute;
    opacity: 0;
    top: 450px;
    left: 0;
    z-index: 15;
    -webkit-transition: opacity 2s cubic-bezier(.33,.66,.66,1);
    transition: opacity 2s cubic-bezier(.33,.66,.66,1);
  }


.title-content{
  text-align: center;
  margin: 70px 0 0 0;
  position: absolute;
  z-index: 20;
  width: 100%;
  top: 0;
  left: 0;
}
h3{
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 2px;
  color: $h-color;
  font-family: 'Roboto', sans-serif;
  margin-bottom: 0;
}
hr{
  width: 50px;
  height: 3px;
  margin: 20px auto;
  border: 0;
  background: $yellow;
}
.intro{
  width: 170px;
  margin: 0 auto;
  color: $txt-color;
  font-family: 'Droid Serif', serif;
  font-size: 13px;
  font-style: italic;
  line-height: 18px;
}


.utility-info{
  position: absolute;
  bottom: 0px;
  left: 0;
  z-index: 20;
}
.utility-list{
  list-style-type: none;
  margin: 0 0 30px 20px;
  padding: 0;
  width: 100%;

  li{
    margin: 0 15px 0 0;
    padding: 0 0 0 22px;
    display: inline-block;
    color: $txt-color;
    font-family: 'Roboto', sans-serif;

    &.comments{
      background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/1765/icon-chat.svg) no-repeat 0 .1em;
    }
    &.date{
      background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/1765/icon-calendar.svg) no-repeat 0 .1em;
    }
  }
}

